.card-without-link {
  background: var(--theme-primary-light);
  padding: 10px;
  border-radius: 5px;
  display: block;
  text-decoration: none;
  margin-bottom: 20px;
  font-size: 18px;
  min-height: 320px;
}

.card-without-link img {
  width: 50%;
  margin: 30px auto;
}

.card-without-link h5 {
  color: var(--theme-primary);
  display: block;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
}

@media only screen and (max-width: 600px) {
  .card-without-link img {
    width: 100%;
  }
  .card-without-link {
    text-align: left;
  }
  .card-without-link {
    min-height: 100px;
  }
}
